.form-sign-in{width:500px;height:auto;padding:15px;background:#fff;box-shadow:0px 4px 20px rgba(0,0,0,.08);border-radius:16px}.form-sign-in__title{font-family:"Roboto";font-weight:400;font-style:normal;font-size:20px;line-height:23px;color:#151317;margin-bottom:16px}.form-sign-in__name{margin-bottom:16px}.form-sign-in__name>label{margin-bottom:9px}.form-sign-in__email{margin-bottom:31px}.form-sign-in__email>label{margin-bottom:9px}.form-sign-in__password{position:relative;margin-bottom:16px}.form-sign-in__password>label{margin-bottom:8px}.form-sign-in__password-pic{position:absolute;top:40px;right:10px}.form-sign-in__confirm-password{margin-bottom:16px}.form-sign-in__confirm-password>label{margin-bottom:8px}.form-sign-in__label{display:block;font-family:"Roboto";font-weight:400;font-style:normal;font-size:16px;line-height:22px;color:#151317}.form-sign-in__error{font-family:"Roboto";font-weight:400;font-style:normal;font-size:10px;line-height:12px;color:#ff6161}.input-field{width:100%;height:48px;padding:12px 8px 12px 16px;border-radius:8px;background:#f8f8f8;font-family:"Roboto";font-weight:400;font-style:normal;font-size:14px;line-height:16px;color:#808185}.input-field--error{border:1px solid #ff6161}.input-field--error::before{position:absolute;content:"Ошибка";font-family:"Roboto";font-weight:400;font-style:normal;font-size:10px;line-height:12px;color:#ff6161}.input-password{width:100%;height:48px;padding:12px 8px 12px 16px;border-radius:8px;background:#f8f8f8;font-family:"Roboto";font-weight:400;font-style:normal;font-size:14px;line-height:16px;color:#808185}